.body-overlay {
    opacity: 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: -1;
    pointer-events: none;
    -webkit-backface-visibility: hidden;
    @include transition(opacity .3s ease);
}

body.side-panel-open .body-overlay {
    opacity: 1;
    z-index: 298;
    pointer-events: auto;
