/*-----------------------------------------------------------------------------------
	sass/responsive.scss
-----------------------------------------------------------------------------------*/

@media only screen and (max-width: 1199px) {
    canvas {
        max-width: 100%;
        height: auto !important;
    }
    .promo {
        h3 {
            font-size: 20px;
        }
    }
    .promo>span,
    .promo>.container>span {
        font-size: 15px;
    }
    .promo-uppercase {
        h3 {
            font-size: 19px;
        }
    }
    .promo-uppercase>span,
    .promo-uppercase>.container>span {
        font-size: 14px;
    }
}

@media only screen and (min-width: 992px) and (max-width: 1199px) {
    #wrapper {
        width: 1000px;
    }
    #header {
        &.transparent-header {
            &.floating-header {
                .container {
                    width: 1030px;
                    padding: 0 30px;
                }
                &.sticky-header {
                    .container {
                        width: 970px;
                        padding: 0 15px;
                    }
                }
            }
        }
    }
    #primary-menu {
        &.sub-title {
            ul {
                >li {
                    >a {
                        padding-left: 15px;
                        padding-right: 15px;
                    }
                }
            }
        }
    }
    #primary-menu ul li.mega-menu .mega-menu-content,
    .floating-header.sticky-header #primary-menu ul li.mega-menu .mega-menu-content {
        width: 940px;
    }
    .floating-header {
        #primary-menu {
            ul {
                li {
                    &.mega-menu {
                        .mega-menu-content {
                            width: 1030px;
                        }
                    }
                }
            }
        }
    }
    .ei-slider {
        height: 450px;
    }
    .postcontent {
        width: $postcontent;
        &.bothsidebar {
            width: $bothsidebar-md;
        }
    }
    .sidebar {
        width: $sidebar-md;
        .widget {
            iframe {
                width: $sidebar-md !important;
            }
        }
    }
    .team {
        &.team-list {
            .team-image {
                width: 200px;
            }
        }
    }
    .testi-image,
    .testi-image a,
    .testi-image img,
    .testi-image i {
        display: block;
        width: 36px;
        height: 36px;
    }
    .testi-image {
        i {
            line-height: 36px;
            font-size: 20px;
        }
    }
    /* ----------------------------------------------------------------
	Portfolio
	-----------------------------------------------------------------*/
    /* Portfolio - Sidebar
	-----------------------------------------------------------------*/
    /* Portfolio - Sidebar
	-----------------------------------------------------------------*/
    .bothsidebar .portfolio-item {
        width: 50%;
    }
    /* Portfolio - Both Sidebars - Items - 2 Columns
	-----------------------------------------------------------------*/
    .bothsidebar .portfolio.portfolio-3 {
        margin: 0 -20px -20px 0;
    }
    .bothsidebar .portfolio-3 .portfolio-item {
        padding: 0 20px 20px 0;
    }
    /* Portfolio - Items - 1 Column
	-----------------------------------------------------------------*/
    .portfolio-1 .portfolio-item .portfolio-image,
    .portfolio-1 .portfolio-item .portfolio-image a,
    .portfolio-1 .portfolio-item .portfolio-image img {
        width: 560px;
        height: 311px;
    }
    .portfolio-1 .portfolio-desc {
        width: 340px;
    }
    .bothsidebar .portfolio-shuffle,
    .postcontent .portfolio-1 .iconlist {
        display: none;
    }
    .postcontent .portfolio-1 .portfolio-item .portfolio-image,
    .postcontent .portfolio-1 .portfolio-item .portfolio-image a,
    .postcontent .portfolio-1 .portfolio-item .portfolio-image img {
        width: 400px;
        height: 222px;
    }
    .postcontent .portfolio-1 .portfolio-desc {
        width: 250px;
        padding: 0;
    }
    .bothsidebar .portfolio-1 .portfolio-item {
        width: 100% !important;
    }
    .bothsidebar .portfolio-1 .portfolio-item .portfolio-image,
    .bothsidebar .portfolio-1 .portfolio-item .portfolio-desc {
        float: none;
        margin: 0 !important;
    }
    .bothsidebar .portfolio-1 .portfolio-item .portfolio-image {
        margin-bottom: 20px !important;
    }
    .bothsidebar .portfolio-1 .portfolio-item .portfolio-image,
    .bothsidebar .portfolio-1 .portfolio-item .portfolio-image a,
    .bothsidebar .portfolio-1 .portfolio-item .portfolio-image img {
        width: 100%;
        height: auto;
    }
    .bothsidebar .portfolio-1 .portfolio-desc {
        width: 100%;
        padding: 0;
    }
    .bothsidebar .entry-title h2 {
        font-size: 19px;
        font-weight: 600;
    }
    .bothsidebar .entry-meta li {
        margin-right: 10px;
    }
    /* ----------------------------------------------------------------
		Blog
	-----------------------------------------------------------------*/
    .postcontent {
        .small-thumbs {
            .entry-image {
                width: 200px;
            }
        }
        .post-grid {
            &.grid-3 {
                .entry {
                    width: blog-grid-item-width($postcontent-md, 3, $blog-3-margin-sb);
                }
            }
            &.grid-2 {
                .entry {
                    width: blog-grid-item-width($postcontent-md, 2, $blog-2-margin-sb);
                }
            }
        }
    }
    .bothsidebar {
        .small-thumbs {
            .entry-image {
                width: 120px;
            }
        }
        .post-grid {
            &.grid-2 {
                .entry {
                    width: blog-grid-item-width($bothsidebar-md, 2, $blog-2-margin-bs);
                }
            }
        }
    }
    .post-grid {
        .entry {
            width: blog-grid-item-width(940px, 4, $blog-4-margin);
        }
        &.grid-3 {
            .entry {
                width: blog-grid-item-width(940px, 3, $blog-3-margin);
            }
        }
        &.grid-2 {
            .entry {
                width: blog-grid-item-width(940px, 2, $blog-2-margin);
            }
        }
    }
    .post-timeline {
        .entry {
            width: 400px !important;
            &.entry-date-section {
                width: 100% !important;
            }
        }
    }
    .mpost .entry-image,
    .mpost .entry-image a,
    .mpost .entry-image img,
    .mpost .entry-image i {
        width: 120px;
        height: 90px;
    }
    .mpost {
        .entry-image {
            a {
                i {
                    line-height: 90px;
                }
            }
        }
    }
    .col_one_third {
        .landing-wide-form {
            padding: 36px 30px;
            .heading-block {
                h2 {
                    font-size: 32px;
                }
                span {
                    font-size: 17px;
                }
            }
        }
    }
    /* ----------------------------------------------------------------
		Shop
	-----------------------------------------------------------------*/
    .bothsidebar {
        .product-1 {
            .product-image {
                width: 200px;
                height: 267px;
            }
        }
    }
    .product-title {
        h3 {
            font-size: 18px;
        }
    }
    .panel {
        .portfolio-meta {
            li {
                padding-left: 22px;
                span {
                    display: block;
                    margin-left: -21px;
                }
            }
        }
    }
    .portfolio-single-image-full.portfolio-single-thumbs,
    .portfolio-single-image-full .swiper-container {
        height: 400px;
    }
    .bnews-slider {
        width: 817px;
    }
    .landing-form-overlay {
        bottom: -142px;
    }
}

@media only screen and (max-width: 991px) {
    #wrapper {
        width: 100%;
        margin: 0;
        box-shadow: none;
    }
    #header.full-header .container,
    .container-fullwidth {
        padding: 0 15px !important;
        margin: 0 auto;
        width: 750px !important;
    }
    .container {
        &.vertical-middle {
            width: 100% !important;
        }
    }
    .vertical-middle {
        &+.video-wrap {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
        }
    }
    .postcontent,
    .postcontent.bothsidebar,
    .sidebar {
        width: 100%;
        margin-right: 0;
        float: none !important;
        margin-bottom: 40px !important;
        padding-bottom: 40px;
        border-bottom: 1px solid #EEE;
    }
    .sidebar {
        &.col_last {
            float: none;
            margin-bottom: 0 !important;
            padding-bottom: 0;
            border-bottom: 0;
        }
    }
    /* ----------------------------------------------------------------
		Header
	-----------------------------------------------------------------*/
    #header,
    #header-wrap,
    #header.sticky-style-2,
    #header.sticky-style-3,
    #header.sticky-style-2 #header-wrap,
    #header.sticky-style-3 #header-wrap {
        height: auto !important;
    }
    #header.sticky-style-2 #header-wrap,
    #header.sticky-style-3 #header-wrap {
        min-height: 60px;
    }
    #header {
        &.sticky-header {
            #header-wrap {
                position: relative;
            }
        }
        &.transparent-header {
            &.floating-header {
                margin-top: 0;
                .container {
                    width: 750px;
                    padding: 0 15px;
                    @include border-radius(0);
                }
            }
            &+#page-title {
                &.page-title-parallax {
                    .container {
                        padding-top: 0;
                    }
                }
            }
        }
    }
    #header.transparent-header,
    #header.semi-transparent {
        background: #FFF;
        border-bottom: 1px solid #F5F5F5;
        z-index: auto;
    }
    #header.dark.transparent-header,
    #header.dark.semi-transparent {
        background-color: #333;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    }
    #header.transparent-header+#slider,
    #header.transparent-header+#page-title.page-title-parallax,
    #slider+#header.transparent-header,
    #header.transparent-header.floating-header+#slider,
    #header.transparent-header+#google-map {
        top: 0;
        margin-bottom: 0;
    }
    .slider-parallax .slider-parallax-inner {
        position: relative;
    }
    #header-trigger,
    .top-advert,
    .header-extras {
        display: none;
    }
    #logo {
        display: block;
        height: 100px;
        float: none;
        margin: 0 auto 0 !important;
        max-width: none;
        text-align: center;
        border: 0 !important;
        padding: 0 !important;
        a {
            &.standard-logo {
                display: inline-block;
            }
        }
    }
    /* ----------------------------------------------------------------
		Primary Menu
	-----------------------------------------------------------------*/
    #primary-menu {
        display: block;
        float: none;
    }
    #primary-menu-trigger {
        opacity: 1;
        pointer-events: auto;
        top: 25px;
        margin-top: 0;
        left: 0;
        z-index: 1;
    }
    #primary-menu .container #primary-menu-trigger {
        top: 5px;
        left: 0;
    }
    #primary-menu>ul,
    #primary-menu>div>ul {
        display: none;
        float: none !important;
        border: 0 !important;
        padding: 0 !important;
        margin: 0 !important;
        @include transition(none);
    }
    #primary-menu>div>ul {
        margin-top: 60px !important;
    }
    body:not(.top-search-open) #primary-menu-trigger {
        opacity: 1;
        @include transition(opacity .2s .2s ease, top .4s ease);
    }
    body.top-search-open #primary-menu-trigger {
        opacity: 0;
    }
    body.side-push-panel #side-panel-trigger-close a {
        display: block;
        position: absolute;
        z-index: 12;
        top: 0;
        left: auto;
        right: 0;
        width: 40px;
        height: 40px;
        font-size: 18px;
        line-height: 40px;
        color: #444;
        text-align: center;
        background-color: rgba(0, 0, 0, 0.1);
        border-radius: 0 0 0 2px;
    }
    #primary-menu.style-5>ul {
        padding-right: 0;
        margin-right: 0;
        border-right: 0;
    }
    #primary-menu ul li {
        float: none;
        margin: 0 !important;
        text-align: left !important;
        border-top: 1px solid #EEE;
    }
    #primary-menu ul li:first-child {
        border-top: 0;
    }
    #primary-menu ul>li>a,
    #primary-menu.style-2>div>ul>li>a {
        padding: 14px 5px !important;
        border: none !important;
        margin: 0 !important;
    }
    #primary-menu.style-5>ul>li>a>div {
        line-height: 22px;
    }
    #primary-menu.style-5>ul>li>a>div i {
        display: inline-block;
        margin: 0 6px 0 0;
        top: -1px;
        width: 16px;
        font-size: 14px;
        line-height: inherit;
    }
    #primary-menu.style-6>ul>li>a:after,
    #primary-menu.style-6>ul>li.current>a:after {
        display: none;
    }
    #primary-menu.sub-title>ul>li,
    #primary-menu.sub-title.style-2>div>ul>li {
        background: none !important;
        margin-left: 0;
    }
    #primary-menu.sub-title>ul>li>a,
    #primary-menu.sub-title>div>ul>li>a {
        line-height: 22px;
        font-size: 13px;
        border-top: none;
    }
    #primary-menu.sub-title ul li>a span {
        display: none !important;
    }
    #primary-menu.sub-title ul li:hover>a,
    #primary-menu.sub-title ul li.current>a,
    #primary-menu.sub-title div ul>li:hover>a,
    #primary-menu.sub-title div ul>li.current>a,
    #primary-menu.style-3 ul li:hover>a,
    #primary-menu.style-3 ul li.current>a {
        background-color: transparent !important;
        color: #444;
        text-shadow: none;
    }
    #primary-menu ul:not(.windows-mobile-menu) li>a i.icon-angle-down:last-child {
        display: none;
    }
    #primary-menu>ul>li.sub-menu>a,
    #primary-menu>.container>ul>li.sub-menu>a {
        background-image: url("../images/icons/submenu.png");
        background-position: right center;
        background-repeat: no-repeat;
    }
    #primary-menu ul li.mega-menu {
        position: relative;
    }
    #primary-menu ul ul:not(.mega-menu-column),
    #primary-menu ul li .mega-menu-content {
        position: relative;
        width: auto;
        max-width: none;
        background: transparent !important;
        box-shadow: none;
        border: 0;
        border-top: 1px solid #EEE;
        z-index: 1;
        top: 0;
        padding-left: 15px;
    }
    #primary-menu ul li .mega-menu-content {
        padding-left: 0;
    }
    #primary-menu ul ul:not(.mega-menu-column) ul {
        top: 0 !important;
        left: 0;
    }
    #primary-menu ul ul.menu-pos-invert:not(.mega-menu-column),
    #primary-menu ul li .mega-menu-content.menu-pos-invert {
        left: 0;
        right: 0;
    }
    #primary-menu ul ul:not(.mega-menu-column) ul.menu-pos-invert {
        right: 0;
    }
    #primary-menu ul ul li {
        float: none;
        margin: 0;
        border-top: 1px solid #EEE;
    }
    #primary-menu ul ul li>a {
        padding: 11px 5px !important;
        font-size: 13px;
        text-transform: none;
        font-weight: 400;
    }
    #primary-menu ul ul li:hover>a {
        background-color: transparent !important;
        font-weight: 600;
        padding-left: 5px;
        color: $theme-color;
    }
    #primary-menu ul li .mega-menu-content,
    #primary-menu ul li.mega-menu-small .mega-menu-content,
    #primary-menu ul li.mega-menu .mega-menu-content,
    .floating-header.sticky-header #primary-menu ul li.mega-menu .mega-menu-content,
    .floating-header #primary-menu ul li.mega-menu .mega-menu-content {
        width: auto;
        margin: 0;
        top: 0;
    }
    .container-fullwidth #primary-menu ul li.mega-menu .mega-menu-content,
    #header.full-header #primary-menu ul li.mega-menu .mega-menu-content {
        width: auto !important;
        margin: 0 !important;
    }
    #primary-menu ul li.mega-menu .mega-menu-content.style-2 {
        padding: 0;
    }
    #primary-menu ul li .mega-menu-content ul {
        display: block !important;
        position: relative;
        opacity: 1 !important;
        top: 0;
        left: 0;
        min-width: inherit;
    }
    #primary-menu ul li .mega-menu-content ul ul {
        border-top: 1px solid #EEE;
    }
    #primary-menu ul li .mega-menu-content ul.mega-menu-column {
        float: none;
        width: auto;
        margin: 0;
        border-left: 0 !important;
        padding-left: 15px;
        border-top: 1px solid #EEE;
    }
    #primary-menu ul li .mega-menu-content ul.mega-menu-column:first-child {
        border: 0;
    }
    #primary-menu ul li .mega-menu-content ul {
        width: 100% !important;
    }
    #primary-menu ul li .mega-menu-content.style-2 ul.mega-menu-column {
        padding: 0 0 0 15px;
    }
    #primary-menu ul li .mega-menu-content.style-2 ul.mega-menu-column>li.mega-menu-title {
        margin: 0;
    }
    #primary-menu ul li .mega-menu-content.style-2 ul.mega-menu-column>li.mega-menu-title>a {
        font-size: 13px;
        font-weight: 600;
        text-transform: none !important;
        padding: 11px 5px !important;
        line-height: 22px !important;
        color: #444 !important;
    }
    #primary-menu ul li .mega-menu-content.style-2 ul.mega-menu-column>li.mega-menu-title:hover>a {
        background-color: transparent;
        color: $theme-color !important;
    }
    #primary-menu ul li .mega-menu-content.style-2 ul.mega-menu-column>li.mega-menu-title>a:hover {
        color: $theme-color !important;
    }
    #primary-menu ul li .mega-menu-content.style-2 li {
        border-top: 1px solid #EEE;
    }
    #primary-menu ul li .mega-menu-content.style-2 li:first-child {
        border-top: 0;
    }
    #primary-menu ul li .mega-menu-content.style-2 ul>li>a:hover {
        padding-left: 5px;
    }
    .mega-menu-column .widget {
        padding: 30px 0;
    }
    /* Top Search
	-----------------------------------------------------------------*/
    #top-search {
        margin: 0 !important;
        form {
            height: 100px !important;
            input {
                font-size: 24px;
            }
        }
    }
    #top-search a,
    #top-cart,
    #side-panel-trigger,
    #top-account {
        position: absolute;
        top: 0;
        left: auto;
        right: 15px;
        margin: 40px 0;
    }
    #top-search a {
        right: 43px;
    }
    #side-panel-trigger {
        right: 71px;
    }
    #top-account {
        margin: 33px 0 32px;
        right: 75px;
    }
    #top-account a.btn span {
        display: none;
    }
    #primary-menu {
        >div {
            >#top-search a,
            >#top-cart,
            >#side-panel-trigger {
                margin: 20px 0 !important;
            }
        }
        ul.windows-mobile-menu {
            li.sub-menu {
                position: relative;
            }
            li.sub-menu a {
                background-image: none !important;
            }
            li.sub-menu a.wn-submenu-trigger {
                position: absolute;
                cursor: pointer;
                width: 32px;
                top: 0;
                right: 0;
                z-index: 1;
                padding: 0 !important;
                text-align: center !important;
            }
            .mega-menu-content a.wn-submenu-trigger {
                display: none;
            }
            li.sub-menu a.wn-submenu-trigger i {
                margin: 0 !important;
            }
        }
    }
    #header {
        &.sticky-style-3 {
            #top-cart {
                .top-cart-content {
                    top: 40px;
                }
            }
        }
    }
    /* Page Menu
	-----------------------------------------------------------------*/
    body:not(.sticky-responsive-pagemenu) #page-menu #page-menu-wrap {
        position: relative !important;
        top: 0 !important;
    }
    #page-menu {
        &.pagemenu-active {
            #page-submenu-trigger {
                background-color: rgba(0, 0, 0, 0.2);
            }
            nav {
                display: block;
            }
        }
        nav {
            display: none;
            position: absolute;
            float: none;
            width: 200px;
            top: 43px;
            left: auto;
            right: 15px;
            height: auto;
            background-color: $theme-color;
            z-index: 11;
            ul {
                height: auto;
                background-color: rgba(0, 0, 0, 0.2);
            }
            li {
                float: none;
                a {
                    height: 40px;
                    line-height: 40px;
                    padding: 0 15px;
                }
            }
        }
    }
    #page-submenu-trigger {
        opacity: 1;
        pointer-events: auto;
        color: #FFF;
    }
    .ei-slider {
        height: 350px;
    }
    .ei-title {
        h3 {
            display: none;
        }
    }
    /* Responsive Sticky Header
	-----------------------------------------------------------------*/
    .sticky-responsive-menu {
        #header {
            min-height: 100px;
            @include transition(min-height .4s ease);
        }
        #header-wrap {
            @include translate3d(0,
            0,
            0);
        }
        &.device-touch {
            #header,
            #header-wrap,
            #logo,
            #logo img,
            #top-search a,
            #primary-menu-trigger,
            #top-cart,
            #side-panel-trigger,
            #top-account {
                @include transition(none !important);
            }
        }
    }
    #header.responsive-sticky-header:not(.sticky-style-2):not(.sticky-style-3):not(.static-sticky) {
        min-height: 60px;
    }
    .responsive-sticky-header {
        #header-wrap {
            z-index: 199;
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            background-color: #FFF;
            border-bottom: 1px solid #EEE;
            -webkit-backface-visibility: hidden;
        }
        &:not(.static-sticky) #logo,
        &:not(.static-sticky) #logo img {
            height: 60px;
        }
        &:not(.static-sticky) #primary-menu-trigger {
            top: 5px;
        }
        #primary-menu>ul,
        #primary-menu>.container>ul {
            max-height: 300px;
            overflow-y: scroll;
            margin-bottom: 20px;
        }
        #primary-menu>ul,
        #primary-menu>.container>ul {
            margin-bottom: 20px !important;
        }
        &:not(.static-sticky) {
            #top-search a,
            #top-cart,
            #side-panel-trigger {
                margin: 20px 0;
            }
            #top-search form {
                height: 60px !important;
            }
            #top-cart .top-cart-content {
                top: 40px;
            }
        }
    }
    /* Off Canvas Menu
	-----------------------------------------------------------------*/
    body:not(.sticky-responsive-menu) {
        #primary-menu {
            &.mobile-menu-off-canvas {
                >ul,
                >div>ul {
                    position: fixed;
                    display: block;
                    z-index: 499;
                    width: 280px;
                    height: 100%;
                    height: calc(100vh);
                    max-height: none !important;
                    margin: 0 !important;
                    left: 0 !important;
                    top: 0 !important;
                    padding: 35px 25px 60px !important;
                    background-color: #FFF;
                    border-right: 1px solid #EEE !important;
                    overflow-y: scroll;
                    -webkit-overflow-scrolling: touch;
                    @include translate3d(-280px,
                    0,
                    0);
                    -webkit-backface-visibility: hidden;
